<!--CusTable组件doc-->
<!--
实用方式如下：  参考src\views\archives\calcPoint\overview.vue  计量方式
<CusTable :tableData="tableData" @handleSizeChange="handleSizeChange" @handleCurrentChange="handleCurrentChange"></CusTable>

参数解释:
tableData: {Array} 表格的data数组 也就是接口返回的数据 
@handleSizeChange: {String} 分页大小改变的事件名称
@handleCurrentChange: {String} 分页页数的事件名称
-->
<template>
    <div style="width: 100%" class="cus-table">
        <el-table row-key="index" :data="tableData" :header-cell-style="{background:'#E6EAFE',color:'#606266'}"
            :row-class-name="tableRowClassName">
            <el-table-column label="序号" type="index" width="90" />
            <el-table-column label="供电公司名称" prop="name"></el-table-column>

            <el-table-column label="操作" align="center" class-name="cusTable" width="90">
                <template slot-scope="scope">
                    <div class="center">
                        <span class="cus-btn" @click="handClick('编辑',scope.row)" type="text" size="small">编辑
                        </span>
                    </div>
                </template>
            </el-table-column>
        </el-table>
        <div class="page pagination-box">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[10, 20, 50, 100]" :page-size="pageSize" layout="prev, pager, next, jumper,total, sizes"
                :total="total">
            </el-pagination>
        </div>
    </div>

</template>

<script>
    import Table from "../components/Table.vue";
    export default {
        extends: Table,
    }
</script>

<style scoped lang="scss">
    .page {
        display: flex;
        justify-content: flex-end;
    }

    .cus-btn {
        color: #2E4AF7;
        font-size: 14px;
        padding-left: 25px;
        cursor: pointer;
        line-height: 22px;
        position: relative;

        &:first-child {
            padding-left: 0;
        }
    }

    .table-btns {
        cursor: pointer;
        display: flex;
        padding: 0 10px;
        justify-content: space-between;
        align-items: center;

        .edit-btn:hover {
            color: #2E4AF7
        }

        .delete-btn:hover {
            color: #E6A23C
        }

        .center {
            width: 100%;
        }

    }


    /deep/ .active-row {
        background-color: #F5F7FA;
    }
</style>