<!--线路维护-->
<template>
    <div>
        <SearchInputs @searchHand="searchHand" @add="addHand"></SearchInputs>
        <Table :tableData="tableData" @handleSizeChange="handleSizeChange" @handleCurrentChange="handleCurrentChange"
            :pageNum="page.pageNum" @btnClick="btnClick" :pageSize="page.pageSize" :total="total">
        </Table>
        <!--弹窗编辑-->
        <el-dialog :title="doType" custom-class="cusDialog" :visible.sync="this.dialogShow" width="5rem"
            :before-close="handleClose">
            <div class='model-form'>
                <form-create v-model="formObj" :rule="rule" :option="options" @gs-code-select="selectKc">
                </form-create>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" :loading="loading" @click="submit">提 交</el-button>
                <el-button @click="handleClose">关 闭</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    //组件
    import BoxTitle from "@/components/BoxTitle.vue";
    import Index from "../components/Index.vue";
    import Table from "./Table.vue";
    import SearchInputs from "./SearchInputs.vue";
    //接口
    import {
        getBasicLineList,
        postEditBasicLine,
        postAddBasicLine,

        getStationListSearch, //查询变电站

        // exportMeterRead,
    } from "@/api/archives.js";
    //公用函数
    export default {
        extends: Index, //继承通用index
        name: 'kcList',
        components: {
            BoxTitle,
            SearchInputs,
            Table
        },
        data() {
            return {
                options: {
                    form: {
                        labelPosition: 'left',
                        labelWidth: '110px'
                    },
                    submitBtn: false,
                    resetBtn: false
                },
                rule: [{
                    type: 'input',
                    field: "name",
                    title: '线路名称：',
                    value: '',
                    props: {
                        placeholder: '请输入变电站名称',
                    },
                    validate: [{
                        required: true,
                        message: '请输入变电站名称',
                        trigger: 'blur'
                    }],
                }, {
                    type: 'AutoCompleteQuery',
                    field: 'gsCode',
                    title: '变电站名称：',
                    props: {
                        placeholder: '请选择或输入变电站名称搜索',
                        fieldValue: 'code',
                        fieldLabel: 'name',
                        value: '',
                        queryFunc: (keyWord) => {
                            const opt = {
                                pageNum: 1,
                                pageSize: 20,
                                _loading: false, //不显示加载中图标
                            }
                            if (keyWord !== '') {
                                opt.keyWord = keyWord;
                            }
                            return getStationListSearch(opt).then(res => {
                                if (res.data) {
                                    const list = res.data.map(item => ({
                                        ...item,
                                        value: item.name,
                                    }))
                                    return list;
                                } else {
                                    return [];
                                }
                            })
                        }
                    },
                    validate: [{
                        required: true,
                        message: '请选择变电站',
                        trigger: 'blur'
                    }],
                    emit: ['select'],
                }, {
                    type: 'radio',
                    field: "type",
                    title: '线路类型：',
                    value: "专线",
                    options: [{
                            value: "专线",
                            label: "专线",
                        },
                        {
                            value: "公网",
                            label: "公网",
                        },

                    ],
                    validate: [{
                        required: true,
                        message: '请至少选择一项线路类型',
                        trigger: 'blur'
                    }],
                }, {
                    type: 'radio',
                    field: "ulevel",
                    title: '电压级别：',
                    value: "10kV",
                    options: [{
                            value: "10kV",
                            label: "10kV",
                        },
                        {
                            value: "35kV",
                            label: "35kV",
                        },
                    ],
                    validate: [{
                        required: true,
                        message: '请至少选择一项电压级别',
                        trigger: 'blur'
                    }],
                }],
            }
        },
        methods: {
            //操作按钮点击事件
            btnClick(btnName, row) {
                this.currEditRow = row; // 记录当前更改的数据
                this.dialogShow = true;
                this.doType = '编辑';
                this.$nextTick(() => {
                    const data = this.formObj.form
                    for (let key in row) {
                        data[key] = row[key];
                    }
                    this.formObj.exec('gsCode', 'setLabel', row.gsName)
                })
            },
            //新增按钮
            addHand() {
                this.currEditRow = 'new'; // 记录当前更改的数据
                this.dialogShow = true;
                this.doType = '新增';
                this.$nextTick(() => {
                    this.formObj.resetFields();
                    this.formObj.exec('gsCode', 'setLabel', '')
                })
            },
            //默认获取表格数据和搜索数据
            _getData(params = {}) { // 通过接口 获取最新数据 如果无接口 则是第一次加载
                getBasicLineList({
                    ...this.search,
                    ...this.page
                }).then(res => {
                    const data = res.rows ? res.rows : [];
                    this.tableData = data;
                    this.total = res.total;
                })
            },
            //表单提交
            submit() {
                this.formObj.validate((valid, fail) => {
                    if (valid) {
                        const data = this.formObj.formData();
                        //todo 表单验证通过
                        this.loading = true;
                        if (this.currEditRow !== 'new') { //编辑
                            postEditBasicLine({
                                ...this.currEditRow,
                                ...data,
                            }).then(this.notice).catch(err => {
                                this.loading = false; //加载
                                return false
                            })
                        } else { //新增
                            postAddBasicLine({
                                ...data,
                            }).then(this.notice).catch(err => {
                                this.loading = false; //加载
                                return false
                            })
                        }
                    }
                })
            },
            //平台选择事件
            selectKc() {
                this.formObj.validateField('gsCode', () => {})
            },
        }
    }
</script>